.section-container{
    width: 100%;
    height: 100vh;
    scroll-snap-align: start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* background-color: #14142B; */
    /* background-color: #1F1D1D; */
    background-color: #151414;
}

