.body-wrapper{
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    scroll-snap-type: y mandatory;
}

@media only screen and (max-width: 1000px) {
    .section-container{
        height: 100vh;
    }
  }